
import { defineComponent } from 'vue'
import NormalCard from "../atoms/NormalCard.vue"

type zelda = {
    title:string,
    description:string,
    color:string
}

export default defineComponent({
    components:{
        NormalCard
    },
    data(){
        return{
            itemIndex:0,
            items:[] as Array<zelda>
        }
    },
    props:{
        itemsProps:{required:true}
    }
})
